@import "app/variables";

// Page header
.page-header{

  #busca-filtros{
    margin-top: 10px;

    .busca-filtro-param-ref{
      display: none;
    }

    .btn{
      &:focus{
        box-shadow: none;
        outline: none;
      }
    }

    .btn-success, .btn-danger{
      background-color: transparent;
      border-color: #fff;
      &:hover{
        background-color: darken(@sistema-color-primary, 5%) !important;
        box-shadow: none;
      }
    }

    .form-control{
      background-color: transparent;
      border: 1px solid #fff;
      color:#fff;
      &:focus{
        outline: none;
        background-color: darken(@sistema-color-primary, 5%) !important;
        box-shadow: none;
      }
      &::-webkit-input-placeholder{
        color: #fff;
      }
      option{
        padding: 5px;
      }
    }

    .btn-link{
      color: #fff;
      &:hover{
        color: darken(@sistema-color-primary, 15%) !important;
      }
    }
  }

  #filtro-tags{
    margin-top: 5px;
    .label-default{
      background-color: darken(@sistema-color-primary, 5%);
      padding: 5px;
      font-weight: normal;
      font-size: .85em !important;

      a.glyphicon{
        color: #fff;
        display: inline-block;
        margin-left: 5px;
        cursor: pointer;
        &:hover{color: darken(@sistema-color-primary, 10%)}
      }
    }
  }
}

// Page content
.page-content{
  h2{
    color: @sistema-color-primary;
    font-size: 1.4em;
  }

  .banner{
    a{
      display: block;
      background-color: #fff;
    }
  }

  .panel{
    background-color: #fff;
    border-radius: 0;
    .panel-heading{
      h3{
        font-size: 1.3em;
        font-style: italic;
        font-weight: bold;
      }
    }
    .panel-body{
      p{
        font-size: .9em;
      }
      .btn-default{
        border-color: @text-color-base;
        margin-bottom: 10px;
        font-size: 1.2em;
        font-style: italic;
        font-weight: bold;
      }
    }
  }

  // Panel-login
  .panel-login{
    box-shadow: none;
    h4{
      color: @sistema-color-primary;
      font-size: 1.4em;
      font-style: italic;
    }
    .btn-success{
        background-color: @sistema-color-primary;
        border-color: darken(@sistema-color-primary,3%);
    }

    .form-control{
      border: 1px solid #ccc;
    }

    .input-group-addon, .form-control{border-radius: 0}

    .panel-footer{
      background-color: transparent;
    }

  }

  // Panel-cadastro
  .panel-cadastro{
    box-shadow: none;
    h4{
      color: #648C80;
      font-size: 1.4em;
      font-style: italic;
    }

    .btn{
      background-color: #648C80;
      color: #fff;
    }
  }

  // Panel-resultados
  .panel-resultados{
    .panel-heading{
      h3{
        color: @sistema-color-primary;
        margin: 5px 0 0 0;
        font-size: 1.5em;
      }

      .text-info{
        color: #666;
        font-size: .85em;
        font-style: italic;
      }
    }

    .tools-bar{
      text-align: right;
      padding-top: 10px;
      nav{
        display: inline-block;
        vertical-align: middle;
        margin: 0;
        ul{margin: 0;}

        li{
          a{
            border:0;
            color: @sistema-color-secundary;
          }
          &:first-child, &:last-child{
            a{
              background-color: @sistema-color-primary;
              border: 1px solid @sistema-color-primary;
              color: #fff;
              border-radius: 0;
            }
          }
          &:first-child a{margin-right: 10px}
          &:last-child a{margin-left: 10px}
          &.active{
           a{
             background-color: #fff;
             border:1px solid @sistema-color-secundary;
             font-weight: bold;
           }

          }
        }
      }
      .form-control{
        margin: -2px 0 0 0;
        border: 1px solid @sistema-color-secundary;
        border-radius: 0;
      }
    }
    .panel-body{
      h4{
        color: @sistema-color-primary;
        font-size: 1.1em;
        font-weight:bold;
      }

      p{
        color: #666;
        font-size: .9em;
        margin: 0;
        font-style: italic;
      }
    }
  }

  // Panel-detalhe
  .panel-detalhe{
    h3{
      color: @sistema-color-primary;
      font-size: 1.6em !important;
    }

    h4{
      color: @sistema-color-primary;
      margin-top: 30px;
    }

    a{
      color: @sistema-color-secundary;
    }

    h5{
      border-bottom:1px solid #ddd ;
      padding-bottom: 10px;
      margin-bottom: 5px;
      small{
        display: block;
        margin-top: 5px;
      }
    }

    ul{
      margin: 0 0 20px 0;
      padding: 0;
      li{
        list-style: none;
        margin: 0;
      }
    }
    tr:last-child{border-bottom: 1px solid #ddd }
    th{white-space: nowrap}
  }
}

// Breadcrumbs
.breadcrumbs{
  ul{
    display: table;
    font-size: .85em;
    margin: 0 0 20px 0;
    padding: 0;
    li{
      float: left;
      list-style: none;
      &:before{
        font-family: 'Glyphicons Halflings';
        content: "\e258";
        font-size: .8em;
        margin: 0 3px;
      }
      &:first-child{
        &:before{content: ""}
      }

      &:last-child{font-weight: bold}

      a{color: @text-color-base}

    }
  }
}

.btn-busca{
  border: 1px solid #fff;
  color:#fff;
  &:hover{
    color:#fff;
    background-color: darken(@sistema-color-primary, 5%);
  }
}




